'use strict'

import { Component, PropTypes} from 'react';
import TableHeader from './TableHeader';
import classnames from 'classnames';
import { substitute } from './utils/strings';

class Grid extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            sort: {}
        };
    }

    onCheck(i, e) {
        let checked = typeof e === 'boolean' ? e : e.target.checked;
        let data = this.props.data.map((d, index) => {
            if (i === 'all' || i === index) {
                d.$checked = checked;
            }
            return d;
        });
        this.setState({data});
    }

    getChecked(name) {
        let values = [];
        this.state.data.forEach((d) => {
            if (d.$checked) {
                values.push(name ? d[name] : d);
            }
        });
        return values;
    }

    renderHeader() {
        let headers = [];
        if (this.props.selectAble) {
            headers.push(
                <TableHeader key="checkbox" name="$checkbox" header={
                    <input onClick={this.onCheck.bind(this, 'all')} type="checkbox" />
                }/>
            );
        }

        this.props.headers.map((header, i) => {
            let props = {
                key: i,
                onSort: (name, asc) => {
                    this.setState({sort: {name, asc}});
                    if (this.props.onSort) {
                        this.props.onSort(name, asc);
                    }
                },
                sort: this.state.sort
            };

            headers.push(
                <TableHeader {...header} {...props} />
            );
        });
        return <tr>{headers}</tr>;
    }

    render() {
        let rows = this.props.data.map((row, i) => {
            return (
                <Row key={i} selectAble={this.props.selectAble} checked={row.$checked}
                     onCheck={this.onCheck.bind(this, i)}
                     onRowClick={this.props.onRowClick}
                     colums={this.props.headers} data={row}/>
            );
        });

        return (
            <table className={classnames(this.props.className, 'table')}>
                <thead>{this.renderHeader()}</thead>
                <tbody>{rows}</tbody>
            </table>
        );
    }
}

Grid.propTypes = {
    className: PropTypes.string,
    data: PropTypes.array.isRequired,
    headers: PropTypes.array.isRequired,
    onSort: PropTypes.func,
    onRowClick: PropTypes.func,
    selectAble: PropTypes.bool
};

class Row extends Component {

    rowTemplate(content, data, name) {
        let body;
        if (typeof content === 'string') {
            body = substitute(content, data);
        } else if (typeof content === 'function') {
            body = content(data);
        } else {
            body = data[name];
        }
        return body;
    }

    onRowClick(e, rowData) {
        if (!!this.props.onRowClick) {
            this.props.onRowClick(e, rowData);
        }
    }

    format() {
        if (!!colum.format && !!colum.format.date) {
            return d instanceof Date ? d.format(colum.format.date) : new Date(d).format(colum.format.date);
        } else {
            return d;
        }
    }

    render() {
        let rowData = this.props.data;
        let selTd;
        if (this.props.selectAble) {
            selTd = (
                <td key="check" style={{width: 13}} key="checkbox">
                    <input checked={this.props.checked} onChange={this.props.onCheck} type="checkbox"/>
                </td>
            );
        }

        return (
            <tr onClick={(e) => {this.onRowClick(e,rowData)}}>
                {selTd}
                {this.props.colums.map((colum, i) => {
                    return <td key={i}>{this.rowTemplate(colum.content, rowData, colum.name)}</td>;
                })}
            </tr>
        );
    }
}

Row.propTypes = {
    data: PropTypes.object.isRequired,
    colums: PropTypes.array.isRequired,
    selectAble: PropTypes.bool,
    checked: PropTypes.bool,
    onRowClick: PropTypes.func,
    onCheck: PropTypes.func
};

Row.defaultProps = {
    selectAble: false,
    checked: false
}

module.exports = Grid;


