import { Component, PropTypes } from 'react';
import {substitute } from './utils/strings';
import Tree from './Tree';

class TreeGrid extends Component {

    constructor(props) {
        super(props);

        this.state = {
            sort: {}
        };
    }

    renderHeader() {
        let headers = this.props.headers.map((header, i) => {
            let style = {};

            if (header.width) {
                if (i == 0) {
                    style.width = header.width + 42 + (!!this.props.selectAble ? 19 : 0);
                } else {
                    style.width = header.width;
                }
            }

            return (
                <th key={i} style={style}>
                    {header.header}
                </th>
            );
        });
        return <tr>{headers}</tr>;
    }

    renderItem(data) {
        let tds = this.props.headers.map((h, j) => {
            if (h.hidden) {
                return;
            }
            let content = h.content,
                tdStyle = {display: "inline-block"};
            if (typeof content === 'string') {
                content = substitute(content, data);
            } else if (typeof content === 'function') {
                content = content(data);
            } else {
                content = data[h.name];
            }
            if (h.width) {
                tdStyle.width = h.width;
            }

            if (j == 0 && tdStyle.width) {
                tdStyle.width -= data.$deep.length * 20 - (!!data.children ? 0 : 19);
            }
            return (<li style={tdStyle} key={j}>{content}</li>);
        });
        return (
            <ul className="app-treegrid-ul" style={{display: "inline-block"}}>
                {tds}
            </ul>);
    }

    render() {
        return (
            <div style={this.props.style} className="app-table app-bordered app-striped">
                <div className="header-container">
                    <div ref="headerContainer">
                        <table ref="header">
                            <thead>{this.renderHeader()}</thead>
                        </table>
                    </div>
                </div>

                <div className="body-container app-treegrid">
                    <Tree {...this.props} textTpl={this.renderItem.bind(this)}/>
                </div>
            </div>
        );
    }
}

TreeGrid.propTypes = {
    className: PropTypes.string,
    data: PropTypes.oneOfType([
        PropTypes.array,
        PropTypes.func
    ]).isRequired,
    greedy: PropTypes.bool,
    onChange: PropTypes.func,
    onClick: PropTypes.func,
    open: PropTypes.bool,
    readOnly: PropTypes.bool,
    selectAble: PropTypes.bool,
    sep: PropTypes.string,
    src: PropTypes.string,
    textTpl: PropTypes.string,
    value: PropTypes.any,
    valueTpl: PropTypes.string
};

import FormControl from './FormControl';
FormControl.register(
    'treegrid',

    function (props) {
        return <TreeGrid {...props} />;
    },

    TreeGrid,

    'array'
);

module.exports = TreeGrid;


