'use strict';

import { Component, PropTypes } from 'react';
import classnames from 'classnames';
let prefix = 'fa';

class Icon extends Component {
  constructor (props) {
    super(props);
    this.state = {
      spin: this.props.spin
    };
  }

  spin () {
    this.setState({ spin: true });
  }

  unspin () {
    this.setState({ spin: false });
  }

  render () {
    let classes = [`${prefix}`];

    if (this.state.spin) {
      classes.push(`fa-spin`);
    }

    if (this.props.icon) {
      classes.push(` fa-`+this.props.icon);
    }

    let size = this.props.size;
    if (size) {
      if (typeof size === 'number' || size.length === 1) {
        classes.push(`${prefix}-${size}x`);
      }
    }

    return (
      <i className={classnames(...classes)} {...this.props}></i>
    );
  }
}

Icon.propTypes = {
  icon: PropTypes.string,
  spin: PropTypes.bool,
};

Icon.setPrefix = function (pre) {
  prefix = pre;
};

module.exports = Icon;



