"use strict";

import React, { Component, PropTypes } from 'react';
import classnames from 'classnames';
import { isEmpty, forEach } from './utils/objects';
import { format, toArray } from './utils/strings';
import merge from './utils/merge';
import DEFAULT_RULES from './utils/validateRules';

const CONTROLS = {};

class FormControl extends Component {
    constructor(props) {
        super(props);

        let validateRules = {};
        forEach(props, (v, k)=> {
            if (k.indexOf('val-') === 0) {
                let r = this.getRules(k, v);
                if (r) {
                    validateRules[r.key] = $.extend(validateRules[r.key], r.value);
                }
            }
        });
        this.__validateRules = validateRules;

        if (DEFAULT_RULES[props.type]) {
            this.__validateRules[props.type] = $.extend(DEFAULT_RULES[props.type], this.__validateRules[props.type] || {});
        }

        this.state = {
            hasError: false,
            focus: false
        };
    }

    getRules(key, value) {
        let s = key.split('-');
        if (s.length < 2) {
            return;
        }
        s.shift();
        let k = s.shift(), obj = {}, temp = obj;
        for (let i = 0; i < s.length; i++) {
            if (i === s.length - 1) {
                temp[s[i]] = value;
            } else if (!temp[s[i]]) {
                temp = temp[s[i]] = {};
            }
        }
        return {key: k, value: obj};
    }

    validate(deferred) {
        if (!this.props.ignore) {
            this.__validate(this.getValue(), function (msg) {
                this.setState({hasError: !!msg, errMsg: msg});
                if (deferred) {
                    if (!!msg) {
                        deferred.reject();
                    } else {
                        deferred.resolve();
                    }
                }
            }.bind(this));
        } else {
            deferred && deferred.resolve();
        }
    }

    __validate(value, callback) {
        let fns = [];
        forEach(this.__validateRules, (v, k) => {
            let rule = $.extend(v, DEFAULT_RULES[k] || {});
            let deferred = $.Deferred();
            let result = rule.fn(value, rule, deferred);
            if (result === true) {
                deferred.resolve(rule);
            } else if (result === false) {
                deferred.reject(rule);
            }
            fns.push(deferred);
        });
        $.when(...fns).then(()=> {
            callback(false);
        }, (arg)=> {
            callback(arg.msg || '请输入正确的格式');
        });
    }

    getValue(sep) {
        return this.refs.control && this.refs.control.getValue(sep);
    }

    handleChange(value) {
        this.validate();
        if (this.props.onChange) {
            this.props.onChange(value);
        }
    }

    handleFocus(focused) {
        this.setState({focus: focused});
    }

    copyProps() {
        let props = {};
        forEach(this.props, (v, k) => {
            props[k] = v;
        });
        props.ref = 'control';
        props.value = this.state.value;
        props.onChange = this.handleChange.bind(this);
        props.onFocus = this.handleFocus.bind(this, true);
        props.onBlur = this.handleFocus.bind(this, false);
        props.defaultValue = this.props.value;
        return props;
    }

    renderWrapper(children) {
        return this.props.wrapperClassName ? (
            <div className={this.props.wrapperClassName} key="wrapper">
                {children}
                {this.state.hasError ? <span className="error">{this.state.errMsg}</span> : this.props.tips}
            </div>
        ) : children;
    }

    renderLabel(children) {
        let classes = classnames("control-label", this.props.labelClassName);
        return this.props.label ? (
            <label htmlFor={this.props.id} className={classes} key="label">{children}{this.props.label}</label>
        ) : children;
    }

    renderControl(props) {
        let control = CONTROLS[this.props.type];
        if (!control) {
            console.warn(`${this.props.type} was not registed.`);
            return null;
        }

        let children = this.props.children;
        if (children) {
            return this.renderChildren(children, control.component);
        } else {
            props = merge(this.copyProps(), props || {});
            return control.render(props);
        }
    }

    renderChildren(children, component) {
        if (!Array.isArray(children)) {
            children = [children];
        }
        let newChildren = [];
        children.map((child, i) => {
            let props = {key: i};
            if (child.type === component) {
                props.ref = 'control';
                props.value = this.state.value;
                props.onChange = this.handleChange.bind(this);
                props.onFocus = this.handleFocus.bind(this, true);
                props.onBlur = this.handleFocus.bind(this, false);
                props.defaultValue = this.props.value;
            }
            if (child.props && typeof child.props.children === 'object') {
                props.children = this.renderChildren(child.props.children, component);
            }
            child = React.cloneElement(child, props);
            newChildren.push(child);
        });
        return newChildren;
    }

    render() {
        return (
            <div className="form-group">
                {[this.renderLabel(), this.renderWrapper(this.renderControl())]}
            </div>
        );
    }
}

// register component
FormControl.register = function (types, render, component, valueType = 'string') {
    if (typeof types === 'string') {
        types = [types];
    }
    types.forEach((type) => {
        CONTROLS[type] = {render, component, valueType};
    });
};

FormControl.propTypes = {
    children: PropTypes.any,
    className: PropTypes.string,
    labelClassName: PropTypes.string,
    wrapperClassName: PropTypes.string,
    value: PropTypes.any,
    id: PropTypes.string,
    label: PropTypes.string,
    name: PropTypes.string,
    onChange: PropTypes.func,
    type: PropTypes.string
};

FormControl.defaultProps = {
    type: 'text'
};

module.exports = FormControl;


