'use strict'

import { Component, PropTypes } from 'react';

import Button from './Button';

class DialogButton extends Component {

    showDialog() {
        if (this.props.src && this.props.target) {
            var $target = $(this.props.target);
            $target.find('iframe').attr('src', this.props.src).attr('height', this.props.height);
            $target.find('.modal-dialog').css('width', this.props.width);
            $target.find('.modal-title').text(this.props.title);
            $target.modal();
        }
    }

    closeDialog() {
        let dialog = $(this.props.target).data('bs.modal');
        if (dialog) {
            dialog.hide();
        }
    }

    onClick() {
        if (this.props.beforeShow) {
            var isShow = this.props.beforeShow(this.props, this.showDialog);
            if (isShow !== true) {
                return;
            }
        }
        this.showDialog();
    }

    render() {
        return (
            <Button {...this.props} onClick={this.onClick.bind(this)} type="button">{this.props.children}</Button>
        );
    }
}

DialogButton.propTypes = {
    className: PropTypes.string,
    beforeShow: PropTypes.func,
    target: PropTypes.string,
    src: PropTypes.string
};

DialogButton.defaultProps = {
    title: '',
    width: 600,
    height: 400,
    target: '#optionDialog'
}

module.exports = DialogButton;


