'use strict';

import React, { Children, Component, PropTypes } from 'react';
import { forEach } from './utils/objects';
import FormControl from './FormControl';
import FormSubmit from './FormSubmit';

class Form extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {}
        };
    }

    componentWillMount() {
        this.fetchData(this.props.data);
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.data !== this.props.data) {
            this.fetchData(nextProps.data);
        }
    }

    fetchData(data) {
        if (typeof data === 'function') {
            data.then((res) => {
                this.fetchData(res);
            })();
            return;
        }
        this.setState({data});
        this.setData(data);
    }

    getValue() {
        let data = this.state.data;
        forEach(this.refs, (ref, k) => {
            if (!ref.props.ignore) {
                data[k] = ref.getValue();
            }
        });
        return data;
    }

    setValue(key, value) {
        let data = this.state.data;
        data[key] = value;
        this.setState({data});
    }

    setData(data) {
        forEach(this.refs, (ref, k) => {
            ref.setValue(data[k]);
        });
    }

    renderChildren() {
        return Children.map(this.props.children, (child) => {
            let props = {
                hintType: child.props.hintType || this.props.hintType,
                readOnly: child.props.readOnly || this.props.locked,
                layout: this.props.layout
            };
            if (child.type === FormControl) {
                if (!child.props.name) {
                    console.warn('FormControl must have a name!');
                    return null;
                }
                props.ref = child.props.name;
                if (this.state.data[child.props.name] !== undefined) {
                    props.value = this.state.data[child.props.name];
                }
            } else if (child.type === FormSubmit) {
                props.locked = this.props.locked;
            }

            child = React.cloneElement(child, props);
            return child;
        });
    }

    getReference(name) {
        return this.refs[name];
    }

    validate(callback) {
        let fns = [];
        forEach(this.refs, (child) => {
            let deferred = $.Deferred();
            if (child.validate) {
                child.validate(deferred);
                fns.push(deferred);
            }
        });
        $.when(...fns).then(()=> {
            callback();
        });
    }

    handleSubmit(event) {
        if (this.props.locked) {
            return;
        }

        event.preventDefault();
        this.onSubmit();
    }

    onSubmit() {
        this.validate(()=> {
            if (this.props.beforeSubmit && !this.props.beforeSubmit()) {
                return;
            }

            if (this.props.onSubmit) {
                this.props.onSubmit(this.getValue());
            }

            this.refs.form.submit();
        });
    }

    render() {
        return (
            <form ref="form" onSubmit={this.handleSubmit.bind(this)} {...this.props}>
                {this.renderChildren()}
            </form>
        );
    }
}

Form.propTypes = {
    beforeSubmit: PropTypes.func,
    children: PropTypes.any,
    className: PropTypes.string,
    data: PropTypes.oneOfType([
        PropTypes.object,
        PropTypes.func
    ]).isRequired,
    hintType: PropTypes.oneOf(['block', 'none', 'pop', 'inline']),
    layout: PropTypes.oneOf(['aligned', 'stacked', 'inline']),
    locked: PropTypes.bool,
    onSubmit: PropTypes.func,
};

Form.defaultProps = {
    data: {},
    layout: 'inline',
    locked: false
};

module.exports = Form;



