import { Component, PropTypes} from 'react';

class Pager extends Component {

    onClick(e, index) {
        if (this.props.onChange && index !== this.props.index) {
            this.props.onChange(e, {index: index, size: this.props.size});
        }
    }

    render() {
        let totalPage = Math.floor((this.props.total - 1) / this.props.size) + 1, prev, next, strs = [];
        let btns = [this.props.index - 2, this.props.index - 1, this.props.index, this.props.index + 1, this.props.index + 2];
        if (totalPage < 1) {
            totalPage = 1
        }
        if (this.props.index > 1) {
            prev = (<a onClick={(e) =>{this.onClick(e,this.props.index - 1)}}
                       className="previous paginate_button">Prev</a>);
        }

        if (btns[0] > 1) {
            strs.push(<a key={strs.length} className="previous paginate_button"
                         onClick={(e) => {this.onClick(e,1)}}>1</a>);
            if (btns[0] > 2) {
                strs.push(<span key={strs.length}>...</span>);
            }
        }

        for (let i = 0; i < btns.length; i++) {
            if (btns[i] >= 1 && btns[i] <= totalPage) {
                strs.push(<a key={strs.length}
                    onClick={((b) =>{return (e) => {this.onClick(e,b)}})(btns[i])}
                    className={btns[i] == this.props.index ? "paginate_active" : "paginate_button"}>{btns[i]}</a>);
            }
        }

        if (btns[btns.length - 1] < totalPage) {
            if (btns[btns.length - 1] < totalPage - 1) {
                strs.push(<span key={strs.length}>...</span>);
            }
            strs.push(<a key={strs.length} onClick={(e) => {this.onClick(e,totalPage)}}
                         className="paginate_button">{totalPage}</a>);
        }

        if (this.props.index < totalPage) {
            next = (<a onClick={(e) => {this.onClick(e,this.props.index + 1)}}
                       className="next paginate_button">Next</a>);
        }
        return (
            <div>
                <div className="dataTables_info">Showing {this.props.index} to {totalPage} of {this.props.total}
                    entries
                </div>
                <div className="dataTables_paginate paging_full_numbers">
                    {prev}<span>{strs}</span>{next}
                </div>
            </div>
        )
    }
}

Pager.propTypes = {
    index: PropTypes.number,
    onChange: PropTypes.func,
    size: PropTypes.number,
    total: PropTypes.number
};

Pager.defaultProps = {
    index: 1,
    size: 20,
    total: 0
};

module.exports = Pager;



