import Regs from './regs';

module.exports = {
    require: {
        fn: (val) => {
            return val !== undefined && val !== null && val !== "";
        },
        msg: "该字段不能为空"
    },
    minlen: {
        fn: (val, params) => {
            return val.length == 0 || val.length >= params.length;
        },
        msg: "字段长度过短"
    },
    maxlen: {
        fn: (val, params) => {
            return val.length <= params.length;
        },
        msg: "字段长度超过最大范围"
    },
    range: {
        fn: (val, params) => {
            return val.length == 0 || (val.length >= params.min && val.length <= params.max);
        }
    },
    reg: {
        fn: (val, params) => {
            return val.length == 0 || params.rule.test(val);
        }
    },
    email: {
        fn: (val) => {
            return val.length == 0 || Regs.email.test(val);
        },
        msg: "请输入正确的邮箱格式，如：admin@126.com"
    },
    url: {
        fn: (val) => {
            return val.length == 0 || Regs.url.test(val);
        },
        msg: "请输入正确的URL格式，如：http://www.baidu.com"
    },
    number: {
        fn: (val) => {
            return val.length == 0 || Regs.number.test(val);
        },
        msg: "请输入正确的数字"
    },
    alpha: {
        fn: (val) => {
            return val.length == 0 || Regs.alpha.test(val);
        }
    },
    alphanum: {
        fn: (val) => {
            return val.length == 0 || Regs.alphanum.test(val);
        }
    },
    password: {
        fn: (val) => {
            return val.length == 0 || Regs.password.test(val);
        }
    },
    integer: {
        fn: (val) => {
            return val.length == 0 || Regs.integer.test(val);
        }
    },
    tel: {
        fn: (val) => {
            return val.length == 0 || Regs.tel.test(val);
        }
    },
    hex: {
        fn: (val) => {
            return val.length == 0 || Regs.hex.test(val);
        }
    },
    rgb: {
        fn: (val) => {
            return val.length == 0 || Regs.rgb.test(val);
        }
    },
    hsv: {
        fn: (val) => {
            return val.length == 0 || Regs.hsv.test(val);
        }
    }
}


